/* 自定義 reset */
a {
    text-decoration: none;
}

img{
	max-width: 100%;
	height: auto;
    vertical-align: middle;
}

/* 全局設定 */
ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

h1,h2,h3,h4,h5,h6,p{
    margin: 0;
}

*, *::after, *::before {
    transition: all .2s;
}
